import React from 'react';
import './Loading.css'

const Loading = ({ language }) => {
  return (
    <div className="loading">
      <p>{language === "" ? "" : "Approving..."}</p>
      <div className="loading-circle">

      </div>
      <p>{language === "" ? "IDO" : "You can contribute IDO after approval"}</p>
      <p>{language === "" ? "" : "Only approved the required allowance"}</p>
    </div>
  );
};

export default Loading;
